@import url(https://fonts.googleapis.com/css?family=Playfair+Display+SC:700&display=swap);
:root {
    --color-primary: #FFEF78;
    --color-primary-rgb: 255, 239, 120;
    --color-dark: #000000;
    --color-dark-rgb: 0, 0, 0;
    --color-light: #FFFFFF;
    --color-light-rgb: 255, 255, 255;
    --font-family-primary: "Helvetica Neue", "sans-serif";
    --font-family-secondary: "Playfair Display SC", "serif";
}
::-moz-selection {
  background-color: var(--color-dark);
  color: var(--color-light);
}

::selection {
  background-color: var(--color-dark);
  color: var(--color-light);
}

body {
  font-family: var(--font-family-primary); 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;  
}

html,
body {
  background-color: var(--color-primary);
  font-weight: 300;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-family-secondary);
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
}

p {
  font-size: 18px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.gift {
    background-color: var(--color-primary);
    border: 1px solid var(--color-dark);
    border-radius: 4px;
    color: var(--color-dark);
    box-sizing: border-box;
    display: block;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    text-decoration: none;
    -webkit-transition: all 304ms;
    transition: all 304ms;
    width: 280px;
}

.gift:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
}

.gift .icon,
.gift .price,
.gift .aprox-price {
    display: block;
}

.gift .icon {
    font-size: 72px;
    padding-bottom: 8px;
}

.gift h3 {
    font-family: var(--font-primary);
    font-size: 18px;
    font-weight: 300;
    padding-bottom: 16px;
}

.gift .price {
    font-size: 20px;
    padding-bottom: 8px;
}

.gift .aprox-price {
    font-size: 14px;
}

@media (min-width: 1024px) {
    .gift {
        width: 200px;
    }
}
.menu {
    box-sizing: border-box;
    left: 0;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    position: absolute;
    top: 0;
    width: 100%;
}

.menu ul {
    align-items: center;
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin-bottom: 0;
    margin-top: 0;
    padding-left: 0;
}

.menu li {
    padding-left: 16px;
    padding-right: 16px;
}


.menu li a {
    color: var(--color-light);
    font-weight: 600;
    text-decoration: none;
    text-transform: uppercase;
}
.modal {
    background-color: var(--color-primary);
    box-shadow: 0 8px 16px rgba(var(--color-dark-rgb), 0.2);
    box-sizing: border-box;
    height: 100vh;
    left: 0;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    position: fixed;
    top: 0;
    -webkit-transition: -webkit-transform 600ms ease-in;
    transition: -webkit-transform 600ms ease-in;
    transition: transform 600ms ease-in;
    transition: transform 600ms ease-in, -webkit-transform 600ms ease-in;
    width: 100%;
}

.modal.is--hidden {
    -webkit-transform: translateY(110%);
            transform: translateY(110%);
}

.modal--is-visible {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.modal .modal__header {
    background-color: var(--color-primary);
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    left: 0;
    top: 0;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    position: absolute;
    width: 100%;
}

.modal .modal__content {
    height: calc(100vh - 56px);
    margin-top: 60px;
    overflow: scroll;
}

.button--close {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    padding-left: 0;
    padding-right: 0;
}

.button--close,
.button-close:active,
.button--close:hover,
.button--close:focus {
    outline: none;
}

.button--close svg {
    height: 24px;
    fill: var(--color-dark);
    width: 24px;
}

@media (min-width: 1024px) {
    .modal {
        left: auto;
        padding-bottom: 24px;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 24px;
        right: 0;
        width: 30%;
    }

    .modal .modal__content {
        margin-top: 28px;
    }

    .modal.is--hidden {
        -webkit-transform: translateX(110%);
                transform: translateX(110%);
    }
    
    .modal--is-visible {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }    
}
.home {
    display: flex;
    flex-direction: column;
}

.picture,
.sidebar {
    box-sizing: border-box;
    width: 100%;
}

.picture {
    align-items: center;
    background-blend-mode: multiply;
    background-color: var(--color-primary);
    background-image: url(https://ucarecdn.com/62d49504-9de0-4a02-b7d8-a2be71a26599/-/progressive/yes/-/quality/lightest/pamellucas.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 150%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    padding-bottom: 32px;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 32px;
}

.picture .logo {
    fill: var(--color-light);
    margin-top: -32px;
}

.button {
    border: 0;
    border-radius: 24px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    outline: none;
    padding-bottom: 12px;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 12px;
    text-transform: uppercase;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.button:hover,
.button:active,
.button:focus {
    outline: none;
}

.button.button--primary {
    background-color: var(--color-dark);
    color: var(--color-light);
}

.button.button--default {
    background-color: var(--color-primary);
    border: 1px solid var(--color-dark);
    color: var(--color-dark);
}

.sidebar {
    background-color: var(--color-primary);
    padding-bottom: 32px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 32px;
}

.sidebar .information:first-child {
    padding-top: 0;
}

.sidebar .information:last-child:after {
    display: none;
}

.information {
    padding-top: 32px;
}

.information:after {
    background-color: var(--color-dark);
    content: "";
    display: block;
    height: 1px;
    margin-top: 32px;
    width: 100%;
}

.information h2 {
    font-size: 24px;
    padding-bottom: 24px;
}

.information p {
    padding-bottom: 16px;
}

.information p:last-child {
    padding-bottom: 0;
}

.information .button {
    margin-top: 8px;
}

.modal__content h1 {
    padding-bottom: 24px;
}

.gift__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 48px;
}

.gift__list .gift {
    margin-bottom: 48px;
}


@media (min-width: 1024px) {
    .home {
        flex-direction: row;
    }

    .picture {
        background-size: cover;
        width: 70%;
    }

    .picture .logo {
        margin-top: -140px;
        width: 560px;
    }

    .picture .menu,
    .picture .button {
        display: none;
    }

    .sidebar {
        height: 100vh;
        overflow: scroll;
        padding-bottom: 64px;
        padding-left: 64px;
        padding-right: 64px;
        padding-top: 64px;
        width: 30%;
    }

    .information {
        padding-top: 48px;
    }

    .information:after {
        margin-top: 48px;
    }

    .gift__list .gift {
        margin-bottom: 24px;
        margin-left: 12px;
        margin-right: 12px;
    }    
}
